import { Component, OnInit } from '@angular/core';
import { FlashcardComponent } from '../flashcard/flashcard.component';
import { FlashcardsSidenavComponent } from '../flashcards-sidenav/flashcards-sidenav.component';

@Component({
  selector: 'app-flashcards',
  templateUrl: './flashcards.component.html',
  styleUrls: ['./flashcards.component.scss']
})
export class FlashcardsComponent implements OnInit {

  public cardIndex: number = 0

  public flashcards: any[] = [
    {
      front: 'card1 front',
      back: 'card1 back'
    },
    {
      front: 'card2 front',
      back: 'card2 back'
    },
    {
      front: 'card3 front',
      back: 'card3 back'
    },
    {
      front: 'card4 front',
      back: 'card4 back'
    }
  ]

  constructor() {
  }

  ngOnInit() {
  }

  public nextCard() {
    if (this.cardIndex + 1 < this.flashcards.length)
      this.cardIndex += 1

    console.log(this.flashcards[this.cardIndex]);
  }

  public prevCard() {
    if (this.cardIndex - 1 >= 0)
      this.cardIndex -= 1
    
    console.log(this.flashcards[this.cardIndex]);
  }

}



