import { Component, OnInit } from '@angular/core';
import { Element } from '../../models/element';

@Component({
  selector: 'app-newstack',
  templateUrl: './newstack.component.html',
  styleUrls: ['./newstack.component.scss']
})
export class NewstackComponent implements OnInit {

  public elements: Element [] = [ new Element() ]

  constructor() { }

  ngOnInit() {
  }

  public addElement() {
    this.elements.push(new Element())
  }

}



